	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__video_wrapper {
		position: relative;
	}

	&__btn {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 10;

		@include reset-btn();

		&:hover {
			opacity: 0.72;
		}
	}

	&__icon {
		width: 80px;
		height: 80px;
		display: block;

		@include media-breakpoint-up(lg) {
			width: 138px;
			height: 138px;
		}
	}

	&__video {
		width: 100%;
		height: 199px;
		object-fit: cover;
		display: block;
		max-height: 540px;

		@include media-breakpoint-up(sm) {
			height: auto;
		}
	}

	&__title {
		text-align: center;
		padding-bottom: 18px;

		@include media-breakpoint-up(lg) {
			padding-bottom: 10px;
		}
	}

	&__text {
		color: $c-lbrown;
		text-align: center;
		padding-bottom: 30px;

		@include media-breakpoint-up(lg) {
			padding-bottom: 36px;
		}
	}

	&__submit {
		padding-top: 13px;
		text-align: right;

		@include media-breakpoint-up(lg) {
			padding-top: 3px;
		}
	}

