	&__list {
		display: flex;
	}

	&__item {
		position: relative;

		&:not(:last-child) {
			margin-right: 40px;
		}
	}

	&__link {
		@include reset-btn();
		font-size: 15px;
		line-height: 32px;
		text-transform: uppercase;
		color: $c-creme;

		&:hover,
		&:focus {
			opacity: 0.72;
		}

		&--active {
			color: $c-gold;
		}
	}

	&__icon {
		width: 12px;
		height: 12px;
		transition: 0.3s;
	}

	&__item:hover &__icon {
		transform: rotateZ(180deg);
	}

	&__sublist {
		position: absolute;
		bottom: 0;
		left: -10px;
		background-color: $c-creme;
		transform: translate(0, 200%);
		z-index: 10;
		opacity: 0;
		transition: 0.3s;
		pointer-events: none;
	}
	
	&__item:hover &__sublist {
		opacity: 1;
		transform: translate(0, 100%);
		pointer-events: all;
	}

	&__sublink {
		display: block;
		line-height: 32px;
		padding: 0 10px;
		text-decoration: none;
		color: $c-brown;
		white-space: nowrap;
		text-transform: uppercase;

		&:hover {
			opacity: 0.72;
		}
	}

