	&__link {
		position: relative;
		display: inline-flex;
		padding-right: 10px;
		min-width: 42px;
		min-height: 42px;
		align-items: center;
		justify-content: center;

		&:hover,
		&:focus {
			opacity: 0.72;
		}
	}

	&__icon {
		width: 24px;
		height: 24px;

		@include media-breakpoint-up(lg) {
			width: 18px;
			height: 18px;
		}
	}

	&__count {
		position: absolute;
		width: 22px;
		height: 22px;
		background-color: $c-gold;
		border-radius: 50%;
		color: $c-white;
		text-align: center;
		line-height: 22px;
		top: 2px;
		right: 4px;

		@include media-breakpoint-up(lg) {
			width: 18px;
			height: 18px;
			top: 4px;
    		right: 7px;
		}
	}

