/**
 * Styles for the hero component.
 */
.hero {
	$block: &;
	position: relative;

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__bg {
		position: absolute;
		z-index: 0;
		left: 0;
		top: 0;
		height: 570px;
		width: 100%;

		@include media-breakpoint-up(lg) {
			height: 660px;
		}

		&:before {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			content: '';
			background: linear-gradient(0deg, rgba(29, 21, 18, 0.7), rgba(29, 21, 18, 0.7))
		}

		&--home {

			@include media-breakpoint-up(lg) {
				height: 800px;
			}
		}
	}

	&__img {
		object-fit: cover;
		object-position: center;
		width: 100%;
		height: 100%;
	}

	&__content {
		position: relative;
		z-index: 1;
		min-height: 570px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		text-align: center;

		@include media-breakpoint-up(lg) {
			min-height: 660px;
		}

		&--home {

			@include media-breakpoint-up(lg) {
				height: 800px;
			}
		}
	}

	&__subtitle {
		font-size: $fs-h3;
		line-height: $lh-h3;
		color: $c-dcreme;
		margin-bottom: $gutter / 2;

		@include media-breakpoint-up(lg) {
			font-size: 28px;
			margin-bottom: 5px;
			letter-spacing: 0.05em;
		}
	}

	&__title {
		color: $c-white;
		margin-bottom: $gutter * 2;

		@include media-breakpoint-up(lg) {
			margin-bottom: 80px;
		}
	}

	&__btn {
		display: flex;
		justify-content: center;
	}

	// BEM modifiers
	// TODO: add code
}
