/// Clear floated content within a container
/// @group helpers
@mixin clearfix() {
  &:after {
    content: ' ';
    display: block;
    clear: both;
  }
}

/// Accessible element hiding
/// @group helpers
@mixin visually-hidden() {
  position: absolute;
  height: 1px;
  margin: -1px;
  padding: 0;
  width: 1px;
  border: 0;
  clip: rect(0 0 0 0);

  overflow: hidden;
}

/// Use a webp background image, but only if the browser supports it
/// @group helpers
@mixin webp-background-image($url) {
  /* Chrome 66+, Edge 79+, Opera 53+, Android Brower 80+ */
  @media screen and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: 0.001dpcm) {
    @supports (background-image: -webkit-image-set(url($url) 1x)) {
      /* stylelint-disable-next-line */
      background-image: -webkit-image-set(url($url) 1x);
    }
  }

  /* FF 66+ */
  @supports ((flex-basis: max-content)) and ((-moz-appearance: meterbar)) {
    background-image: url($url);
  }
}

@mixin reset-list() {
  list-style: none;
  padding: 0;
  margin: 0;
}

@mixin reset-input() {
  background: none;
  border: 1px solid transparent;
  border-radius: 0;
  outline: none;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}

@mixin reset-btn() {
  cursor: pointer;
  padding: 0;
  background: none;
  border: none;
  border-radius: 0;
  outline: none;
  text-decoration: none;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}

@mixin optional-at-root($sel) {
  @at-root #{if(not &, $sel, selector-append(&, $sel))} {
    @content;
  }
}

@mixin placeholder {
  @include optional-at-root('::-webkit-input-placeholder') {
    @content;
  }

  @include optional-at-root(':-moz-placeholder') {
    @content;
  }

  @include optional-at-root('::-moz-placeholder') {
    @content;
  }

  @include optional-at-root(':-ms-input-placeholder') {
    @content;
  }
}