/**
 * Styles for the slider component.
 */
.universes_slider {
	$block: &;
	padding: 50px 0;
	overflow: hidden;

	@include media-breakpoint-up(lg) {
		padding: 80px 0;
	}

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code
	.product__picture {
		margin-bottom: 4px;

		@include media-breakpoint-up(lg) {
			height: 240px;
			margin-bottom: 9px;
		}
	}

	// BEM elements
	// TODO: add code
	&__container {
		overflow: visible;
	}

	&__wrapper {
		max-width: 280px;
		padding-bottom: 18px;

		@include media-breakpoint-up(lg) {
			padding-bottom: 27px;
		}
	}

	&__item {

		@include media-breakpoint-up(lg) {
			max-width: 184px;
		}
	}

	&__content {
		text-align: center;
		padding: 60px 0 50px;
		height: 100%;
		background-color: $c-white;
		position: relative;
		z-index: 1;

		&:before {
			position: absolute;
			left: 0;
			top: 0;
			width: 100vw;
			height: 100%;
			content: '';
			display: block;
			transform: translateX(-100%);
			background-color: $c-white;
		}

		&:after {
			position: absolute;
			right: 0;
			top: 0;
			width: 40px;
			height: 100%;
			content: '';
			display: block;
			transform: translateX(100%);
			background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 100%);
		}

		@include media-breakpoint-up(lg) {
			padding: 0;
			text-align: left;
		}
	}

	&__title {
		margin-bottom: 17px;
	}

	&__text {
		color: $c-lbrown;
		margin-bottom: 34px;
	}

	// BEM modifiers
	// TODO: add code
}
