/**
 * Styles for the filter component.
 */
.filter {
	$block: &;

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__title {
		font-size: $fs-18body;
		line-height: $lh-default;
		color: $c-gold;
		text-transform: uppercase;
		padding: 25px 0 19px;

		@include media-breakpoint-up(lg) {
			padding: 25px 0 17px;
		}
	}

	&__item {
		margin-bottom: 13px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 16px;
		}
	}

	&__devider {
		margin: 25px 0 -1px;
		width: 100%;
		border: 1px solid $c-creme;

		&:last-of-type {
			display: none;
		}

		@include media-breakpoint-up(lg) {
			margin: 22px 0 6px;
			
			&:last-of-type {
				display: block;
			}
		}
	}
	
	&__products {
		padding: 40px 0 20px;

		@include media-breakpoint-up(lg) {
			padding-bottom: 4px;
		}
	}
	
	&__product {
		padding-bottom: 34px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 61px;
		}
	}

	&__action {
		padding-bottom: 40px;
		text-align: center;
	}

	&__content {

		@include media-breakpoint-up(lg) {
			padding: 37px 0;
		}
	}

	// BEM modifiers
	// TODO: add code
}

.filter_mobile {
	position: relative;
	background-color: $c-creme;
	padding: 21px 0 18px;

	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__btn {
		display: flex;
		align-items: center;
		text-decoration: none;
	}
	
	&__text {
		font-size: $fs-18body;
		line-height: $lh-default;
		color: $c-gold;
		text-transform: uppercase;
	}

	&__icon {
		width: 32px;
		height: 32px;
		margin-right: 7px;
	}

	&__modal {
		padding: 32px 40px;
		text-align: left;
	}

	&__btns {
		padding-top: 21px;
		display: flex;

		@include media-breakpoint-up(lg) {
			padding-top: 33px;
		}

		> * {
			min-width: 0;
			flex: 1;
		}
	}
}