/**
 * Styles for the navigation component.
 */
.navigation {
	$block: &;
	display: none;
	width: 32px;
	height: 32px;
	background-color: transparentize($color: $c-white, $amount: 0.6);
	position: absolute;
	top: 164px;
	z-index: 10;
	align-items: center;
	justify-content: center;
	transition: 0.3s;

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

		@include media-breakpoint-up(lg) {
			display: block;
		}

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code

	&__icon {
		width: 24px;
		height: 24px;
	}

	// BEM modifiers
	// TODO: add code

	&--next {
		right: 20px;
	}

	&--prev {
		left: 20px;
	}

	&--disabled {
		opacity: 0;
		pointer-events: none;
	}
}
