/* IE8
	 ========================================================================== */

.lt-ie9 .remodal-overlay {
	background: $c-brown;
}

.lt-ie9 .remodal {
	width: 700px;
}


.modal {
	padding: 70px 30px;

	&.fullwidth {
		padding: 32px 17px;

		@include media-breakpoint-up(lg) {
			padding: 60px 72px;
		}
	}
	
	@include media-breakpoint-up(lg) {
		max-width: 632px;
	}
	
	&__close {
		@include reset-btn();
		
		position: absolute;
		top: 12px;
		right: 12px;
		
		&--filter {
			position: static;
		}
	}

	&.fullwidth &__close {
		position: static;

		@include media-breakpoint-up(lg) {
			position: absolute;
		}
	}
	
	&__close_icon {
		width: 32px;
		height: 32px;
		display: block;
	}
	
	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@include media-breakpoint-up(lg) {
			display: block;
		}
	}

	&.fullwidth &__header {
		padding-bottom: 23px;

		@include media-breakpoint-up(lg) {
			padding-bottom: 38px;
		}
	}
	
	&__title {
		// color: $c-brown;
		// font-size: $fs-h2;
		// line-height: $lh-h2;
		padding-bottom: 10px;
	}
	
	&.fullwidth &__title {
		padding-bottom: 0;
	}
	
	&__text {
		padding-bottom: 26px;
		color: $c-lbrown;
	}

	&__btns {

		*:last-child {
			margin-top: 10px;
		}
	}

	&__form {
		text-align: left;
	}

	&--fw-mobile {

	}
}