/**
 * Styles for the block component.
 */
.universes_block {
	$block: &;
	padding: 60px 0;
	text-align: center;

	@include media-breakpoint-up(lg) {
		padding: 80px 0;
	}

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__picture {
		display: block;
	}

	&__img {
		margin: 0 auto;
		display: block;
	}

	&__content {
		padding: 45px 0 0;
		
		@include media-breakpoint-up(lg) {
			padding: 0;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
		}
	}

	&__subtitle {
		color: $c-lbrown;
		margin-bottom: -3px;

		@include media-breakpoint-up(lg) {
			margin-bottom: -12px;
		}
	}

	&__title {
		font-family: $ff-bellerose;
		color: $c-gold;
		text-transform: uppercase;
		margin-bottom: 31px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 22px;
		}
	}

	&__text {
		margin-bottom: 35px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 40px;
		}
	}

	// BEM modifiers
