/**
 * Styles for the collection nav component.
 */
.collection_nav {
	$block: &;
	padding: 32px 0 27px;
	justify-content: center;

	@include media-breakpoint-up(lg) {
		padding: 35px 0 53px;
	}

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__container {
		overflow: visible;
	}

	&__wrapper {
		padding-bottom: 15px;
	}

	&__item {
		width: auto;

		&:first-child {
			margin-left: 11px;

			@include media-breakpoint-up(lg) {
				margin-left: 0;
			}
		}

		&:last-child {

			@include media-breakpoint-up(lg) {
				margin-right: 0 !important;
			}
		}
	}

	&__link {
		text-decoration: none;
		font-size: $fs-20body;
		line-height: $lh-130body;
		color: $c-lbrown;
		display: inline-block;
		position: relative;

		&:hover {
			color: $c-gold;
		}

		&--active {
			color: $c-gold;
		}

		@include media-breakpoint-up(lg) {
			font-size: $fs-h3;
			padding: 0 11px;

			&:after {
				content: '';
				display: block;
				position: absolute;
				border-bottom: 2px solid $c-gold;
				width: 100%;
				left: 0;
				bottom: -8px;
				transform: translateY(10px);
				opacity: 0;
				transition: ease 0.3s all;
			}

			&--active {

				&:after {
					transform: translateY(0);
					opacity: 1;
				}
			}
		}
	}

	// BEM modifiers
	// TODO: add code
}
