/**
 * Styles for the btn component.
 */
.btn {
	$block: &;
	@include reset-btn();
	text-transform: uppercase;
	font-size: $fs-default;
	line-height: $lh-default;
	border: 1px solid transparent;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	padding: 13px;
	min-width: 230px;
	box-sizing: border-box;

	&:hover,
	&:focus {
		opacity: 0.72;
	}

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code

	// BEM modifiers
	// TODO: add code

	&--hero {
		letter-spacing: 0.05em;
	}

	&--fw-mobile {
		min-width: 100%;

		@include media-breakpoint-up(md) {
			min-width: 230px;
		}
	}

	&--subscribe_btn {
		font-size: $fs-14body;
		line-height: $lh-default;
		color: $c-brown;
		background-color: $c-creme;
		border-color: $c-creme;
		padding: $gutter / 2 $gutter / 3;
		min-width: auto;

		@include media-breakpoint-up(lg) {
			padding: $gutter / 3 $gutter / 2;
			font-size: 12px;
			line-height: 18px;
		}

		&:hover,
		&:focus {
			background-color: $c-dcreme;
			border-color: $c-dcreme;
		}
	}

	&--creme {
		background-color: $c-creme;
		border-color: $c-creme;
		color: $c-brown;
	}

	&--lbrown {
		color: $c-lbrown;
		border-color: $c-lbrown;
	}

	&--brown {
		background-color: $c-brown;
		border-color: $c-brown;
		color: $c-white;
		letter-spacing: 0.05em;
	}

	&--brown-border {
		border-color: $c-brown;
		color: $c-brown;
		min-width: 200px;
	}

	&--gold {
		background-color: $c-gold;
		border-color: $c-gold;
		color: $c-white;
	}

	&--secondary {
		color: $c-gold;
	}

	&--add-to-cart,
	&--brown-border,
	&--small {

		@include media-breakpoint-up(lg) {
			font-size: 12px;
			line-height: $lh-default;
			min-width: 200px;
			padding: 10px;
		}
	}
}
