	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__picture {
		height: 240px;
		margin-bottom: 22px;

		@include media-breakpoint-up(lg) {
			height: 260px;
			margin-bottom: 20px;
		}
	}

	&__img {
		object-fit: cover;
		object-position: center;
		width: 100%;
		height: 100%;
		display: block;
	}

	&__title {
		margin-bottom: 7px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 14px;
		}
	}

	&__date {
		color: $c-lbrown;
		line-height: $lh-default;

		@include media-breakpoint-up(lg) {
			font-size: $fs-18body;
		}
	}

	&__link {
		text-decoration: none;
		display: block;

		&:hover {
			opacity: 0.72;
		}
	}

