strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* box-sizing fix */
/* stylelint-disable-next-line no-duplicate-selectors */
body {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* root font size */
:root {
  font-size: 1em;
}

/* universal max-width rule for images */
img {
  max-width: 100%;
}

/* remove default focus styles */
a,
button,
input {
  outline: 0;
  -webkit-tap-highlight-color: transparent;
}

a::-moz-focus-inner,
button::-moz-focus-inner,
input::-moz-focus-inner {
  outline: 0;
  border: 0;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

figure {
  overflow: hidden;
}

figure.block__picture img, figure.product__picture img, figure.article__picture img {
  transition: 10s ease;
}

figure.block__picture:hover img, figure.product__picture:hover img, figure.article__picture:hover img {
  transform: scale(1.1);
}

