  background-color: #F1EBE7;
}

.universes_nav__scrollbar_drag {
  background-color: #C5BBB6;
}

/**
 * Styles for the block component.
 */
.universes_block {
  padding: 60px 0;
  text-align: center;
}

@media (min-width: 992px) {
  .universes_block {
    padding: 80px 0;
  }
}

.universes_block__picture {
  display: block;
}

.universes_block__img {
  margin: 0 auto;
  display: block;
}

.universes_block__content {
  padding: 45px 0 0;
}

@media (min-width: 992px) {
  .universes_block__content {
    padding: 0;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
}

.universes_block__subtitle {
  color: #A69B95;
  margin-bottom: -3px;
}

@media (min-width: 992px) {
  .universes_block__subtitle {
    margin-bottom: -12px;
  }
}

.universes_block__title {
  font-family: "Bellerose", sans-serif;
  color: #A0894E;
  text-transform: uppercase;
  margin-bottom: 31px;
}

@media (min-width: 992px) {
  .universes_block__title {
    margin-bottom: 22px;
  }
}

.universes_block__text {
  margin-bottom: 35px;
}

@media (min-width: 992px) {
  .universes_block__text {
    margin-bottom: 40px;
  }
}

/**
 * Styles for the slider component.
 */
.universes_slider {
  padding: 50px 0;
  overflow: hidden;
}

@media (min-width: 992px) {
  .universes_slider {
    padding: 80px 0;
  }
}

.universes_slider .product__picture {
  margin-bottom: 4px;
}

@media (min-width: 992px) {
  .universes_slider .product__picture {
    height: 240px;
    margin-bottom: 9px;
  }
}

.universes_slider__container {
  overflow: visible;
}

.universes_slider__wrapper {
  max-width: 280px;
  padding-bottom: 18px;
}

@media (min-width: 992px) {
  .universes_slider__wrapper {
    padding-bottom: 27px;
  }
}

@media (min-width: 992px) {
  .universes_slider__item {
    max-width: 184px;
  }
}

.universes_slider__content {
  text-align: center;
  padding: 60px 0 50px;
  height: 100%;
  background-color: #FFFFFF;
  position: relative;
  z-index: 1;
}

.universes_slider__content:before {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100%;
  content: '';
  display: block;
  transform: translateX(-100%);
  background-color: #FFFFFF;
}

.universes_slider__content:after {
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 100%;
  content: '';
  display: block;
  transform: translateX(100%);
  background: linear-gradient(90deg, #020024 0%, white 0%, rgba(255, 255, 255, 0) 100%);
}

@media (min-width: 992px) {
  .universes_slider__content {
    padding: 0;
    text-align: left;
  }
}

.universes_slider__title {
  margin-bottom: 17px;
}

.universes_slider__text {
  color: #A69B95;
  margin-bottom: 34px;
}

/**
 * Styles for the collection nav component.
 */
.collection_nav {
  padding: 32px 0 27px;
  justify-content: center;
}

@media (min-width: 992px) {
  .collection_nav {
    padding: 35px 0 53px;
  }
}

.collection_nav__container {
  overflow: visible;
}

.collection_nav__wrapper {
  padding-bottom: 15px;
}

.collection_nav__item {
  width: auto;
}

.collection_nav__item:first-child {
  margin-left: 11px;
}

@media (min-width: 992px) {
  .collection_nav__item:first-child {
    margin-left: 0;
  }
}

@media (min-width: 992px) {
  .collection_nav__item:last-child {
    margin-right: 0 !important;
  }
}

.collection_nav__link {
  text-decoration: none;
  font-size: 20px;
  line-height: 130%;
  color: #A69B95;
  display: inline-block;
  position: relative;
}

.collection_nav__link:hover {
  color: #A0894E;
}

.collection_nav__link--active {
  color: #A0894E;
}

@media (min-width: 992px) {
  .collection_nav__link {
    font-size: 22px;
    padding: 0 11px;
  }
  .collection_nav__link:after {
    content: '';
    display: block;
    position: absolute;
    border-bottom: 2px solid #A0894E;
    width: 100%;
    left: 0;
    bottom: -8px;
    transform: translateY(10px);
    opacity: 0;
    transition: ease 0.3s all;
  }
  .collection_nav__link--active:after {
    transform: translateY(0);
    opacity: 1;
  }
}

/**
 * Styles for the hero collection component.
 */
.collection_hero {
  text-align: center;
  padding: 60px 0;
