h1,
h2,
h3,
h4 {
	color: currentColor;
}

h1 {
	font-size: $fs-h1-mobile;
	line-height: $lh-h1;
	color: $c-brown;

	@include media-breakpoint-up(lg) {
		font-size: $fs-h1;
	}
}

h2 {
	font-size: $fs-h2-mobile;
	line-height: $lh-h2-mobile;
	color: $c-brown;

	@include media-breakpoint-up(lg) {
		font-size: $fs-h2;
		line-height: $lh-h2;
	}
}

h3 {
	font-size: $fs-h3-mobile;
	line-height: $lh-h3;
	color: $c-brown;

	@include media-breakpoint-up(lg) {
		font-size: $fs-h3;
	}
}