/**
 * Styles for the section component.
 */
.section {
	$block: &;
	padding: 60px 0;

	@include media-breakpoint-up(lg) {
		padding: 81px 0;
	}

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__picture {
		margin: 0 auto 7px;
	}

	&__img {
		display: block;
		margin: 0 auto;
	}

	&__title {
		text-align: center;
		padding-top: 20px;
		padding-bottom: 43px;
	}

	&__btn {
		text-align: center;
		padding-top: 8px;
		padding-bottom: 40px;
	}

	&__product {
		padding-bottom: 60px;
	}

	// BEM modifiers
	// TODO: add code
	&--collection_description {
		font-size: $fs-18body;
		line-height: $lh-default;
		text-align: center;
		color: $c-brown;

		@include media-breakpoint-up(lg) {
			font-size: $fs-lbody;
		}
	}

	&--event {
		padding: 62px 0;

		@include media-breakpoint-up(lg) {
			padding: 66px 0;
		}
	}
}
