  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: $gutter / 3 0;
  }

  &__actions {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex: 1;
    padding-top: 5px;

    @include media-breakpoint-up(lg) {
      padding-top: 0;
    }
  }

  &__auth {
    transform: translate(5px, 0px);

		@include media-breakpoint-up(lg) {
			transform: translate(12px, -1px);
    }
  }

  &__logo {
    flex: 1;
    max-width: none;
  }

  &__lang {
    margin-right: 30px;
    min-height: 42px;
    min-width: 42px;
    align-items: center;
  }

