/**
 * Styles for the events component.
 */
.events {
	$block: &;
	padding: 60px 0;

	&:last-child {
		padding-bottom: 100px;
	}

	@include media-breakpoint-up(lg) {
		padding: 80px 0;
	}

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__item {
		
		&:not(:last-child) {
			padding-bottom: 60px;

			@include media-breakpoint-up(lg) {
				padding-bottom: 40px;
			}
		}
	}

	&__main_title {
		text-align: center;
		padding-bottom: 40px;

		@include media-breakpoint-up(lg) {
			text-align: left;
			padding-bottom: 46px;
		}
	}

	&__picture {
		display: block;
	}

	&__img {
		display: block;
		width: 100%;
	}

	&__category {
		font-size: 12px;
		line-height: 30px;
		color: $c-dcreme;
		letter-spacing: 0.3em;
		text-transform: uppercase;
		padding-top: 16px;

		@include media-breakpoint-up(md) {
			padding-top: 0;
			font-size: 14px;
			line-height: 30px;
			margin-top: 7px;
		}
	}

	&__title {
		font-size: $fs-h3-mobile;
		line-height: $lh-h3;
		color: $c-brown;
		letter-spacing: -0.02em;
		padding-bottom: 14px;
	}

	&__text {
		font-size: $fs-default;
		line-height: $lh-default;
		color: $c-lbrown;
		padding-bottom: 8px;

		@include media-breakpoint-up(md) {
			padding-bottom: 11px;
		}
	}

	&__date {
		font-size: $fs-default;
		line-height: $lh-default;
		color: $c-dcreme;
	}

	&__btn {
		padding-top: 32px;

		@include media-breakpoint-up(md) {
			padding-top: 21px;
		}
	}

	// BEM modifiers
	// TODO: add code
}
