/**
 * Styles for the block component.
 */
.block {
	$block: &;
	background-color: $c-creme;
	text-align: center;
	overflow: hidden;

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__img {
		display: block;
		object-fit: cover;
		object-position: center;
		width: 100%;
		height: 100%;
	}

	&__picture {
		height: 490px;

		@include media-breakpoint-up(lg) {
			height: 630px;
			flex: 1;
		}
	}

	&__content {
		padding: 82px 0;
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;

		&--product {
			padding: 42px 0 54px;
		}

		&--gallery {
			padding: 52px 0 0;

			.container {
				padding: 0;
			}

			@include media-breakpoint-up(lg) {
				padding-top: 150px;
				padding-bottom: 162px;
			}
		}

		&--ingredients {
			padding-bottom: 44px;
		}

		&--advice {
			display: block;
			padding: 61px 0 47px;
			position: relative;
			
			@include media-breakpoint-up(lg) {			
				padding: 43px 0;
			}
		}
	}

	&__subtitle {
		color: $c-gold;
		margin-bottom: $gutter / 3;

		@include media-breakpoint-up(lg) {
			margin-bottom: 6px;
			// TODO: remove
			margin-bottom: 11px;
		}
	}

	&__title {
		color: $c-brown;
		margin-bottom: 26px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 23px;
		}

		&--dark {
			color: $c-white;
		}
	}

	&__text {
		color: $c-lbrown;
		font-size: $fs-18body;
		line-height: 29px;
		margin-bottom: $gutter;

		@include media-breakpoint-up(lg) {
			max-width: 520px;
			margin: 0 auto 39px;
		}

		&--dark {
			color: $c-creme;
		}
	}

	&__btn {
		display: flex;
		justify-content: center;
	}

	&__price {
		color: $c-gold;
		font-size: $fs-lbody;
		line-height: $lh-default;
		margin-top: -8px;
		margin-bottom: 16px;
		display: flex;
		justify-content: center;
		align-items: baseline;

		@include media-breakpoint-up(lg) {
			font-size: $fs-h2;
			line-height: $lh-h2;
			margin-bottom: 20px;
			margin-top: -1px;
		}

		&--dark {
			color: $c-white;
		}
	}

	&__action {
		text-decoration: none;
		font-size: $fs-default;
		line-height: $lh-default;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		margin-top: 25px;
		color: #A6917A;
		display: inline-block;

		&:hover {
			opacity: 0.72;
		}
	}

	&__gallery {
		padding-top: 60px;
		flex: 1;
		padding-bottom: 16px;
		display: flex;

		@include media-breakpoint-up(lg) {
			padding-top: 130px;
		}
	}

	&__thumbs_container {
		display: none;
		width: 92px;
		flex-shrink: 0;
		margin-right: 10px;

		@include media-breakpoint-up(lg) {
			display: block;
		}
	}

	&__product_container {
		overflow: visible;
		flex: 1 1 auto;
		width: 0;
		
		@include media-breakpoint-up(lg) {
			overflow: hidden;
		}
	}

	&__product_wrapper {
		padding-bottom: 12px;
	}

	&__atributes {
		font-size: $fs-18body;
		line-height: $lh-default;
		color: $c-brown;
		margin-top: -1px;
    	margin-bottom: 13px;

		@include media-breakpoint-up(lg) {
			margin-top: 0;
			margin-bottom: 1px;
		}
	}

	&__counter {
		margin-top: -7px;
		margin-bottom: 41px;

		@include media-breakpoint-up(lg) {
			margin-top: -17px;
			margin-bottom: 20px;
		}
	}

	&__ingredients_picture {
		display: block;
		position: absolute;
		
		&:nth-child(1) {
			width: 41%;
			height: 33%;
			right: 9%;
    		top: 28%;
			box-shadow: 0px 5.79683px 38.9968px rgba(82, 60, 51, 0.4);
			z-index: 20;

			@include media-breakpoint-up(lg) {
				right: 0;
				width: 46%;
				top: 39%;
				height: 47%;
			}
		}
		
		&:nth-child(2) {
			width: 27%;
			height: 32%;
			left: 27%;
    		top: 39%;
			box-shadow: 0px 5.79683px 21.0794px rgba(82, 60, 51, 0.2);
			z-index: 19;

			@include media-breakpoint-up(lg) {
				width: 30%;
				left: 28%;
				height: 45%;
				top: 55%;
			}
		}
		
		&:nth-child(3) {
			width: 54%;
			height: 48%;
			left: 9%;
			top: 0;
			z-index: 18;

			@include media-breakpoint-up(lg) {
				width: 61%;
				left: 8%;
				height: 68%;
			}
		}
		
		&:nth-child(4) {
			width: 35%;
			height: 29%;
			right: 14%;
			top: 8%;
			box-shadow: 0px 2.10794px 2.10794px rgba(0, 0, 0, 0.25);
			z-index: 17;

			@include media-breakpoint-up(lg) {
				width: 38%;
				height: 40%;
				right: 5%;
				top: 10%;
			}
		}
	}

	&__ingredients_wrap {

		@include media-breakpoint-up(sm) {
			height: 500px;
			padding: 60px 0;
		}

		@include media-breakpoint-up(md) {
			height: 640px;
		}

		@include media-breakpoint-up(lg) {
			height: 470px;
		}

		@include media-breakpoint-up(xl) {
			height: 550px;
		}

		@include media-breakpoint-up(xxl) {
			height: 630px;
		}
	}

	&__ingredients {
		position: relative;
		min-height: calc(100vw - 34px);

		@include media-breakpoint-up(sm) {
			min-height: 0;
			height: 500px;
		}

		@include media-breakpoint-up(md) {
			height: 640px;
		}

		@include media-breakpoint-up(lg) {
			height: 470px;
		}

		@include media-breakpoint-up(xl) {
			height: 550px;
		}

		@include media-breakpoint-up(xxl) {
			height: 510px;
		}

		&--count3, &--count5 {
			margin-top: -18px;
			min-height: calc(100vw - 19px);

			@include media-breakpoint-up(sm) {
				min-height: 0;
				margin-top: -30px;
				height: 500px;
			}
	
			@include media-breakpoint-up(md) {
				height: 640px;
			}
	
			@include media-breakpoint-up(lg) {
				height: 390px;
			}
	
			@include media-breakpoint-up(xl) {
				height: 470px;
			}
	
			@include media-breakpoint-up(xxl) {
				height: 510px;
			}
		}
	}

	&__ingredients--count3 &__ingredients_picture {

		&:nth-child(1) {
			width: 42%;
			height: 45%;
			left: -1%;
			top: 24%;

			@include media-breakpoint-up(lg) {
				width: 33%;
				height: 47%;
				left: 9%;
				top: 39%;
			}
		}

		&:nth-child(2) {
			width: 55%;
			height: 51%;
			left: 12%;
			top: -4%;

			@include media-breakpoint-up(lg) {
				width: 44%;
				height: 54%;
				left: 20%;
				top: 10%;
			}
		}

		&:nth-child(3) {
			width: 81%;
			height: 69%;
			left: 20%;
			top: 11%;

			@include media-breakpoint-up(lg) {
				width: 65%;
				height: 73%;
				left: 26%;
				top: 25%;
			}
		}
	}

	&__ingredients--count5 &__ingredients_picture {

		&:nth-child(1) {
			width: 28%;
			height: 23%;
			right: -1%;
			top: 21%;

			@include media-breakpoint-up(lg) {
				width: 26%;
				height: 28%;
				right: 0%;
				top: 28%;
			}
		}

		&:nth-child(2) {
			width: 29%;
			height: 21%;
			left: -1%;
			top: 43%;

			@include media-breakpoint-up(lg) {
				width: 28%;
				height: 27%;
				left: 4%;
				top: 54%;
			}
		}

		&:nth-child(3) {
			width: 31%;
			height: 28%;
			left: 14%;
			top: 52%;

			@include media-breakpoint-up(lg) {
				width: 29%;
				height: 35%;
				left: 18%;
				top: 66%;
			}
		}

		&:nth-child(4) {
			width: 35%;
			height: 28%;
			right: 6%;
			top: 4%;

			@include media-breakpoint-up(lg) {
				width: 34%;
				height: 35%;
				right: 6%;
				top: 6%;
			}
		}

		&:nth-child(5) {
			width: 72%;
			height: 61%;
			left: 14%;
			top: 11%;

			@include media-breakpoint-up(lg) {
				width: 68%;
				height: 75%;
				left: 18%;
				top: 15%;
			}
		}
	}

	&__ingredients_img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
	}

	&__advice {
		font-size: 14px;
		line-height: 20px;
		color: $c-lbrown;
		padding-top: 60px;
		
		@include media-breakpoint-up(lg) {
			position: absolute;
			bottom: 28px;
			width: calc(100% - 110px);
			left: 50%;
			transform: translateX(-50%);
		}
	}

	&__list {
		display: flex;
		justify-content: space-between;
		margin-top: -2px;
		padding-bottom: 30px;
		
		@include media-breakpoint-up(lg) {
			margin-top: -16px;
			justify-content: center;
			padding-bottom: 24px;
			max-width: 410px;
			margin-left: auto;
			margin-right: auto;
		}
	}

	&__icon {
		width: 24px;
		height: 24px;
		margin-bottom: 11px;
		
		@include media-breakpoint-up(lg) {
			margin-bottom: 5px;
		}
	}

	&__list_item {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		padding: 0 10px;
		color: $c-gold;
		font-size: $fs-14body;
		line-height: 18px;
		flex: 1;

		@include media-breakpoint-up(lg) {
			padding: 0 15px;
		}
	}

	&__from {
		font-size: $fs-14body;
		line-height: $lh-14body;
		margin-right: 12px;

		&--dark {
			color: $c-white;
		}
	}

	// BEM modifiers
	// TODO: add code
	// &--reverse {
	// 	flex-direction: row-reverse;
	// }

	// &--reverse_mobile {

	// 	& > .container {
	// 		display: flex;
	// 		flex-direction: column-reverse;
	
	// 		@include media-breakpoint-up(lg) {
	// 			flex-direction: row-reverse;
	// 		}
	// 	}
	// }

	&--gallery {
		padding-bottom: 80px;

		@include media-breakpoint-up(lg) {
			padding-bottom: 0;
		}
	}

	&--dark {
		background-color: $c-brown;
	}
}
