.sort_mobile {

	@include media-breakpoint-up(lg) {
		position: relative;
	}
	
	&__text {
		font-size: $fs-18body;
		line-height: $lh-default;
		color: $c-gold;
		text-transform: uppercase;
		display: flex;
		cursor: pointer;

		@include media-breakpoint-up(lg) {
			text-transform: unset;
		}
	}

	&__active {
		margin-left: 5px;
	}

	&__dropdown {
		position: absolute;
		bottom: 0;
		left: 0;
		min-width: 100%;
		background-color: $c-nwhite;
		transform: translateY(200%);
		z-index: 2;
		overflow: hidden;
		opacity: 0;
		transition: 0.3s;
		pointer-events: none;
		box-shadow: 0px 16px 50px rgba(91, 68, 54, 0.2);

		&--active {
			pointer-events: all;
			opacity: 1;
			transform: translateY(100%);
		}
	}

	&__list {
		padding-top: 16px;
	}

	&__link {
		font-size: $fs-18body;
		line-height: 49px;
		text-transform: uppercase;
		text-decoration: none;
		color: $c-brown;
		display: block;
		white-space: nowrap;

		&--active {
			color: $c-gold;
		}
	}
}