/**
 * Styles for the counter component.
 */
.counter {
	$block: &;
	display: flex;
	align-items: center;
	justify-content: center;

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__btn {
		@include reset-btn();
		display: block;
		padding: 0;
		width: 44px;
		height: 44px;
		flex-shrink: 0;
		background-color: $c-nwhite;
		border: 1px solid $c-lbrown;

		@include media-breakpoint-up(lg) {
			width: 28px;
			height: 28px;
		}
	}

	&__count {
		@include reset-input();
		font-size: $fs-lbody;
		line-height: $lh-default;
		color: $c-brown;
		text-align: center;
		flex: 0 1 56px;
    	width: 56px;
		padding: 0 5px;
		border: none;

		@include media-breakpoint-up(lg) {
			line-height: 28px;
			width: 40px;
			flex-basis: 40px;
		}
	}

	&__icon {
		width: 40px;
		height: 40px;

		@include media-breakpoint-up(lg) {
			width: 26px;
			height: 26px;
		}
	}

