// Fonts
$ff-default: 'Futura Bk BT', sans-serif;
$ff-bellerose: 'Bellerose', sans-serif;
// Base font size and line height
$fs-default: 16px;
$lh-default: 160%;

$lh-130body: 130%;

$fs-14body: 14px;
$lh-14body: 30px;

$fs-18body: 18px;

$fs-20body: 20px;
$lh-20body: 130%;

$fs-lbody: 26px;

$fs-h1: 84px;
$fs-h1-mobile: 52px;
$lh-h1: 110%;

$fs-h2: 38px;
$fs-h2-mobile: 32px;
$lh-h2: 130%;
$lh-h2-mobile: 110%;

$fs-h3: 22px;
$fs-h3-mobile: 20px;
$lh-h3: 130%;

// Font weight values
$fw-400: 400;
$fw-700: 700;

// Color palette
$c-grey: #B1B6C1;
$c-dgrey: #808592;
$c-brown: #5B4436;
$c-lbrown: #A69B95;
$c-gold: #A0894E;
$c-white: #FFFFFF;
$c-creme: #F1EBE7;
$c-dcreme: #C5BBB6;
$c-nwhite: #FEFDFA;

// grid
$gutter: 30px;
$grid-gutter-width: 40px;
$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
  xxl: 1340px
);
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
);