/**
 * Styles for the pagination component.
 */
.pagination {
	$block: &;
	display: flex;
	align-items: center;
	justify-content: center;
	padding-bottom: 100px;

	@include media-breakpoint-up(lg) {
		justify-content: flex-end;
		padding-bottom: 63px;
	}

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__icon {
		width: 24px;
		height: 24px;
		display: block;

		@include media-breakpoint-up(lg) {
			width: 20px;
			height: 20px;
		}
	}

	&__arrow {
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid transparent;
		width: 33px;
		height: 33px;

		&:not(:last-child) {
			margin-right: 11px;
		}

		&:hover {
			border-color: $c-gold;
		}

		@include media-breakpoint-up(lg) {
			width: 28px;
			height: 28px;
		}
	}

	&__link {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 33px;
		height: 33px;
		margin-right: 11px;
		color: $c-gold;
		text-decoration: none;
		border: 1px solid transparent;

		@include media-breakpoint-up(lg) {
			width: 28px;
			height: 28px;
		}

		&--active {
			color: $c-white;
			background-color: $c-gold;
		}

		&:hover {
			border-color: $c-gold;
		}
	}

