/**
 * Styles for the lang component.
 */
.lang {
	$block: &;
	cursor: pointer;
	position: relative;

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__active {
		display: flex;
		align-items: center;
		font-size: 15px;
		line-height: $lh-default;
		text-transform: uppercase;
		color: $c-dcreme;
	}

	&:hover &__active {
		opacity: 0.72;
	}

	&__link {
		display: block;
		line-height: 32px;
		padding: 0 10px;
		text-decoration: none;
		color: $c-brown;
		text-transform: uppercase;

		&:hover {
			opacity: 0.72;
		}
	}

	&__icon {
		width: 12px;
		height: 12px;
		margin-left: 8px;
		transition: 0.3s;
	}

	&:hover &__icon {
		transform: rotateZ(180deg);
	}

	&__list {
		position: absolute;
		bottom: 0;
		left: -10px;
		background-color: $c-creme;
		transform: translate(0, 200%);
		z-index: 10;
		opacity: 0;
		transition: 0.3s;
		pointer-events: none;
	}
	
	&:hover &__list {
		opacity: 1;
		transform: translate(0, 100%);
		pointer-events: all;
	}

	// BEM modifiers
	// TODO: add code
}

