/**
 * Styles for the universes_nav component.
 */
.universes_nav {
	$block: &;
	padding: 24px 0 40px;
	background-color: $c-nwhite;
	overflow: hidden;

	@include media-breakpoint-up(xxl) {
		padding-bottom: 0;
	}

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__container {
		overflow: visible;
	}

	&__wrapper {
		padding-bottom: 28px;

		@include media-breakpoint-up(lg) {
			padding-bottom: 24px;
		}
	}

	&__item {
		text-align: center;
		width: auto;

		&:not(:hover) {
			opacity: 0.72;
		}
	}

	&__link {
		text-decoration: none;
		display: block;
		padding-bottom: 40px;
		position: relative;
	}

	&__picture {
		display: block;
	}

	&__img {
		display: block;
		margin: 0 auto;
	}

	&__title {
		font-size: $fs-14body;
		line-height: 120%;
		text-transform: uppercase;
		font-family: $ff-bellerose;
		color: $c-lbrown;
		white-space: nowrap;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
	}

	&__item:hover &__title {
		color: $c-gold;
	}

	&__scrollbar,
	&__scrollbar_drag {
		height: 4px;
		border-radius: 10px;
	}
	
	&__scrollbar {
		background-color: $c-creme;
	}

	&__scrollbar_drag {
		background-color: $c-dcreme;
	}

	// BEM modifiers
	// TODO: add code
}
