/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  // content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* box-sizing fix */
/* stylelint-disable-next-line no-duplicate-selectors */
body {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* root font size */
:root {
  font-size: ($fs-default / 16px) + em;
}

/* universal max-width rule for images */
img {
  max-width: 100%;
}

/* remove default focus styles */
a,
button,
input {
  outline: 0;
  -webkit-tap-highlight-color: transparent;

  &::-moz-focus-inner {
    outline: 0;
    border: 0;
  }
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

figure {
  overflow: hidden;

  &.block__picture,
  &.product__picture,
  &.article__picture {

    img {
      transition: 10s ease;
    }
  
    &:hover {
  
      img {
        transform: scale(1.1);
      }
    }
  }

}