/**
 * Styles for the nav mobile component.
 */
.nav_mobile {
	$block: &;
	flex: 1;

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__burger {
		transform: translate(-4px, 0px);
	}

	&__close {
		position: absolute;
		top: 14px;
    	left: 13px;
	}

	&__burger,
	&__close {
		@include reset-btn();
		width: 42px;
		height: 42px;
		display: flex;
		align-items: center;
		justify-content: center;
		
		&:hover,
		&:focus {
			opacity: 0.72;
		}
	}

	&__icon {
		width: 34px;
		height: 27px;
	}

	&__arrow {
		width: 20px;
		height: 20px;
	}

	&__arrow_before {
		width: 20px;
		height: 20px;
		position: absolute;
		left: -100%;
		top: 50%;
		opacity: 0;
		transform: translateY(-50%);
	}

	&__container {
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background-color: $c-brown;
		z-index: 1000;
		transform: translateX(-100%);
		transition: ease 0.3s transform;
		pointer-events: none;

		&--active {
			transform: translateX(0);
			pointer-events: all;
		}
	}

	&__wrapper {
		min-height: 100vh;
		display: flex;
    	flex-direction: column;
		padding: 93px 21px 23px;
	}

	&__list {
		flex: 1;
	}

	&__item {
		margin-bottom: 40px;
		max-height: 75px;
		transition: ease 0.3s all;
		position: relative;
		
		&--hidden {
			max-height: 0;
			margin: 0;
			padding: 0;
			overflow: hidden;
		}
	}

	&__link {
		@include reset-btn();
		font-size: $fs-h2-mobile;
		line-height: $lh-h2-mobile;
		color: $c-nwhite;
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
		transition: ease 0.3s padding-left;
		transition-delay: 0s;
		
		&--active {
			transition-delay: 0.3s;
			padding-left: 34px;
		}
	}
	
	&__link--active &__arrow {
		opacity: 0;
		transform: translateX(-100%);
	}
	
	&__link--active &__arrow_before {
		left: 0;
		opacity: 1;
	}

	&__sublist {
		position: absolute;
		bottom: -24px;
		transform: translate(100%, 100%);
		opacity: 0;
		width: 100%;
		transition: ease 0.3s all;
		transition-delay: 0;

		&--active {
			opacity: 1;
			transform: translate(0, 100%);
			transition-delay: 0.3s;
		}
	}
	
	&__subitem {
		display: block;
	}

	&__sublink {
		@include reset-btn();
		font-size: $fs-20body;
		line-height: $lh-20body;
		color: $c-nwhite;
		display: block;
		padding: 15px 0;
	}

	// BEM modifiers
	// TODO: add code
}
