/**
 * Styles for the form component.
 */
.form {
	$block: &;

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__row {
		display: flex;
	}

	&__controls {
		position: relative;
		flex: 1;
		width: 100%;
		padding-bottom: 20px;

		&--subscribe_btn {
			flex: 0;
			width: auto;
			margin-left: 10px;
			padding-bottom: 0;
		}
	}

	&__label {
		font-size: $fs-default;
		line-height: $lh-default;
		color: $c-brown;
		display: inline-flex;
		margin-bottom: $gutter / 6;

		@include media-breakpoint-up(lg) {
			margin-bottom: 2px;
		}

		&--checkbox {
			font-size: $fs-20body;
			line-height: $lh-20body;
			color: $c-brown;
			align-items: center;
		}

		&--small {
			font-size: $fs-14body;
			line-height: $lh-default;
			margin-bottom: 1px;
		}

		&--dcreme {
			color: $c-dcreme;
		}
	}

	&__input {
		@include reset-input();
		border-color: $c-dcreme;
		min-height: 42px;
		width: 100%;
		padding: 6px 20px;
		font-family: $ff-default;
		font-size: $fs-18body;
		line-height: $lh-default;
		color: $c-brown;

		@include placeholder {
			font-family: $ff-default;
			font-size: $fs-18body;
			line-height: $lh-default;
			color: $c-lbrown;
		}

		&:focus {
			border-color: $c-dcreme;
		}

		&--checkbox {
			position: absolute;
			visibility: hidden;
			width: 0;
			height: 0;
		}

		&--dcreme {
			border-color: $c-dcreme;
		}

		&--subscribe {
			min-height: 54px;
			color: $c-creme;

			@include media-breakpoint-up(lg) {
				min-height: 40px;
			}
		}

		&--textarea {
			min-height: 100px;
			resize: none;
		}
	}

	&__box {
		width: 24px;
		height: 24px;
		border: 1px solid $c-dcreme;
		background: $c-nwhite;
		box-sizing: border-box;
		margin-right: 10px;
		display: flex;
		align-items: center;
		justify-content: center;

		@include media-breakpoint-up(lg) {
			width: 18px;
			height: 18px;
		}
	}

	&__input--checkbox:checked + &__label &__box {
		border-color: $c-brown;
		background-color: $c-brown;
	}

	&__icon {
		width: 18px;
		height: 18px;
	}

	// BEM modifiers
	// TODO: add code
}
