/**
 * Styles for the footer social component.
 */
.social {
	$block: &;

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__list {
		@include reset-list();
		display: flex;
		padding-top: 16px;

		@include media-breakpoint-up(lg) {
			padding-top: 20px;
		}
	}

	&__item {
		
		&:not(:last-child) {
			margin-right: 21px;
		}
	}

	&__link {
		font-size: $fs-20body;
		line-height: $lh-20body;
		text-decoration: none;
		color: $c-creme;

		&:hover,
		&:focus {
			opacity: 0.72;
		}
	}

	&__icon {
		width: 40px;
		height: 40px;

		@include media-breakpoint-up(lg) {
			width: 30px;
			height: 30px;
		}
	}

	&__title {
		font-size: $fs-20body;
		line-height: $lh-20body;
		color: $c-creme;
		margin-right: 20px;
		padding-top: 9px;
	}

	// BEM modifiers
	// TODO: add code
	&--mobile {
		display: flex;
		align-items: center;
	}
}
