	&__list {
		display: flex;
		padding-top: 28px;
	}

	&__link,
	&__item {
		color: $c-lbrown;
		text-decoration: none;
		font-size: $fs-14body;
		line-height: $lh-14body;
	}

	&__link {

		&:hover {
			opacity: 0.72;
		}
	}

	&__item {

		&:not(:last-child) {
			margin-right: 5px;
		}
	}

	&__icon {
		width: 10px;
		height: 10px;
		margin-left: 3px;
		transform: translateY(1px);
	}

