/**
 * Styles for the product component.
 */
.product {
	$block: &;
	position: relative;
	text-decoration: none;
	display: block;

	@include media-breakpoint-up(sm) {
		text-align: center;
	}

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code
	&:hover {

		@include media-breakpoint-up(lg) {
			background-color: $c-creme;
		}
	}

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__picture {
		// height: 342px;
		margin-bottom: 11px;

		@include media-breakpoint-up(lg) {
			height: 360px;
			margin-bottom: 19px;
		}

		&--small-mobile {
			margin-bottom: 0;
		}
	}

	&__img {
		object-position: center;
		object-fit: cover;
		width: 100%;
		height: 100%;
		display: block;
	}

	&__title {
		color: $c-brown;
		font-size: $fs-20body;
		line-height: $lh-20body;
		margin-bottom: 7px;
		height: 26px;
		overflow: hidden;
		white-space: nowrap;
		-webkit-line-clamp: 1;
  		-webkit-box-orient: vertical;
		text-overflow: ellipsis;
		text-align: left;

		&--small-mobile {
			overflow: hidden;
			white-space: normal;
			-webkit-line-clamp: 2;
			height: 52px;
			margin-bottom: 9px;
		}

		@include media-breakpoint-up(lg) {
			margin-bottom: 10px;
			text-align: center;
			-webkit-line-clamp: 1;
			height: 26px;

			&--small {
				font-size: $fs-18body;
				height: 46px;
				overflow: hidden;
				white-space: normal;
				-webkit-line-clamp: 2;
				margin: 14px 0 3px;
			}
		}
	}

	&__price {
		color: $c-gold;
		font-size: $fs-18body;
		display: flex;
		align-items: center;
		justify-content: space-between;

		@include media-breakpoint-up(lg) {
			display: block;
			margin-bottom: 9px;

			&--small {
				font-size: $fs-default;
			}
		}
	}

	&__action {
		width: 44px;
		height: 44px;
		border: 1px solid $c-brown;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		box-sizing: border-box;

		&--small-mobile {
			width: 40px;
			height: 40px;
		}
	}

	&__icon {
		width: 24px;
		height: 24px;
	}

	// BEM modifiers
	// TODO: add code
}
